.card {
  border-radius: 18px;
  background: #1d204b;
  padding: 20px;
  position: relative;
}

@media (min-width: 320px) {
  .flex-title {
    display: flex;
    flex-basis: 100%;
    align-self: center;
    align-items: center;
    justify-content: space-between;
  }

  .stats * {
    align-content: center;
    transition: 0.3s;
  }

  .stats {
    margin: 80px auto 30px;
    bottom: 0;
    transition: 0.3s;
  }

  .stats:hover {
    bottom: 20px;
    transition: 0.3s;
  }

  .stats:hover:before {
    top: 0px;
    transition: 0.3s, 0.3s;
    transform: scale(0.90) skew(18deg) rotate(9deg);
    -webkit-transform: scale(0.90) skew(18deg) rotate(9deg);
  }

  .stats:before {
    content: "";
    border-radius: inherit;
    z-index: -1;
    top: -60px;
    left: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    transition: 0.3s;
  }

  .stats.work:before {
    background: url(/time-tracking-dashboard/static/media/icon-work.a3f124e5.svg), #ff8b64;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: -8%;
    background-size: 100px;
  }

  .stats.play:before {
    background: url(/time-tracking-dashboard/static/media/icon-play.e8d4e569.svg), #56c2e6;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: -8%;
    background-size: 100px;
  }

  .stats.study:before {
    background: url(/time-tracking-dashboard/static/media/icon-study.1f0a65e4.svg), #ff5e7d;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: -8%;
    background-size: 100px;
  }

  .stats.exercise:before {
    background: url(/time-tracking-dashboard/static/media/icon-exercise.3e7b0282.svg), #4bcf83;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: 0%;
    background-size: 100px;
  }

  .stats.social:before {
    background: url(/time-tracking-dashboard/static/media/icon-social.64be0c18.svg), #7235d1;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: -8%;
    background-size: 100px;
  }

  .stats.self-care:before {
    background: url(/time-tracking-dashboard/static/media/icon-self-care.a161aff5.svg), #f1c75b;
    background-repeat: no-repeat;
    background-position-x: 80%;
    background-position-y: -8%;
    background-size: 100px;
  }

  .ellipsis {
    opacity: 0.7;
    cursor: pointer;
  }

  .ellipsis:hover {
    opacity: 1;
  }

  .card.stats * {
    margin: 0px;
    align-content: flex-start;
    flex-wrap: wrap;
    padding: 10px 0;
  }

  .card.stats {
    padding: 30px 60px;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    flex-direction: row;
    justify-content: space-between;
  }

  .card.stats .stat-hours-prev {
    align-self: center;
  }

  .stat-title {
    font-size: 24px;
    font-weight: 400;
  }

  .stat-hours {
    font-size: 60px;
    font-weight: 100;
  }

  .stat-hours-prev {
    font-weight: 100;
    font-size: 16px;
    font-weight: 100;
    opacity: 0.7;
  }
}

